#CardShoppingCart{
    width: 100%;
    flex-shrink: 0;
    display: flex;
    align-items: center;
    justify-content: start;
    padding: 10px 2px;
    border-bottom: 1px solid rgb(87, 84, 84);
    color: rgb(207, 206, 203);
    position: relative;
    overflow: hidden;
    font-family: 'Nasalization', sans-serif;
}

#CardShoppingCart>:nth-child(1){
    height: 80px;
    width: 110px;
    display: flex;
    align-items: center;
    justify-content: center;
}

#CardShoppingCart>:nth-child(1)>img{
    width: 60px;
    border-radius: 10px;
}
#CardShoppingCart>:nth-child(2){
    width: 110px;
    font-size: 16.5px;
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    text-align: center;
}

#CardShoppingCart>:nth-child(3){
    display: flex;
    width: 110px;
    height: 100%;
    align-items: center;
    justify-content: center;
    font-size: 16.5px;
}

#CardShoppingCart>:nth-child(3)>span{
    width: 20px;
    height: 20px;
    background-color: #e2eaed;
    border-radius: 4px;
    border: none;
    font-size: 16.5px;
    display: flex;
    align-items: center;
    justify-content: center;
    color: #000000;
    user-select: none;
}
#CardShoppingCart>:nth-child(3)>span:active{
    transform: scale(1.15);
    background-color: #a7a9aa;
}
#CardShoppingCart>:nth-child(3)>p{
    margin: 0 7px;
    font-size: 16.5px;
}
#CardShoppingCart>:nth-child(4){
    width: 110px;
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 16.5px;
    padding-right: 10px;
}
#trashIcon{
    position: absolute;
    right: 5px;
    bottom: 3px;
}
#trashIcon img{
    width: 15px;
    cursor: pointer;
}
