#ShoppingCart{
    display: flex;
    width: 440px;
    align-items: center;
    justify-content: start;
    flex-direction: column;
    font-family: 'Nasalization', sans-serif;
}
#ShoppingCart>:nth-child(1){
    border-bottom: 2px solid #b8b7b7;
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: start;
    color: #b8b7b7;
    font-size: 15px;
    padding: 10px 0 16px 10px;
}
#ShoppingCart>:nth-child(1)>h1{
    font-size: 25px;
    font-weight: 400;
}   
#empty{
    width: 100%;
    text-align: center;
    padding: 30px 0;
    color: #b8b7b7;
}
#empty>p{
    font-size: 20px;

}
#ShoppingCart>:nth-child(3){
    width: 100%;
    height: 60px;
    flex-shrink: 0;
    display: flex;
    align-items: center;
    justify-content: space-between;
    color: rgb(207, 206, 203);
    padding: 0 30px;
}

#ShoppingCart>:nth-child(3)>span{
    background-color: #f96400;
    padding: 3px 5px;
    border-radius: 10px;
    border: none;
    font-size: 17.5px;
    font-weight: 800;
    cursor: pointer;
    display: flex;
    align-items: center;
    justify-content: center;
}
#ShoppingCart>:nth-child(3)>span>a{
    text-decoration: none;
    color: #2c2c25;
    font-weight: 500;
}

#ShoppingCart>:nth-child(3)>h3{
    position: relative;
    font-weight: 300;

}
#ShoppingCart>:nth-child(3)>h3::after{
    background-color: rgb(207, 206, 203);
    width: 150%;
    height: 3px;
    content: '';
    display: block;
    position: absolute;
    top: -5px;
    right: 55%;
    transform: translate(50%,0);
}

#cardContainer{
    padding-right: 20px;
}

 Estilos de la scrollbar 
#cardContainer::-webkit-scrollbar {
    width: 15px; 
    background-color: #b81111d8; 
    border-radius: 10px; 
  }
  
#cardContainer::-webkit-scrollbar-thumb {
    background-color: #888;
    border-radius: 10px; 
  }
  
  #cardContainer::-webkit-scrollbar-thumb:hover {
    background-color: #555; 
  } 